<template>
  <div class="home">
    <!-- 导航栏 -->
    <el-row class="menu">
      <div class="menu_cont">
        <div class="menu_cont_item">
          <div class="menu_cont_item_left .left">
            <i class="el-icon-s-home"></i>闪烁之狐
          </div>
          <div class="menu_cont_item_right">
            <el-menu
              :default-active="activeIndex"
              mode="horizontal"
              @select="handleSelect"
            >
              <el-menu-item index="1">
                <i class="el-icon-s-home"></i>首页</el-menu-item
              >
              <!-- <el-submenu index="2">
                <template slot="title">
                  <i class="el-icon-menu"></i>我的工作台</template
                >
                <el-menu-item index="2-1">选项1</el-menu-item>
              </el-submenu> -->

              <el-menu-item index="2">
                <i class="el-icon-menu"></i>我的工作台</el-menu-item
              >

              <el-menu-item index="3">
                <i class="el-icon-user-solid"></i>关于我</el-menu-item
              >
            </el-menu>
          </div>
        </div>
      </div>
    </el-row>

    <!-- banner图 -->

    <el-carousel height="100vh" :interval="5000" class="banner">
      <el-carousel-item v-for="item in img" :key="item">
        <!-- <h3 class="small">{{ item }}</h3> -->
        <img :src="item.url" alt="" />
      </el-carousel-item>
    </el-carousel>
  </div>
</template>

<script>
export default {
  name: "home",
  data() {
    return {
      activeIndex: "1",
      activeIndex2: "1",
      img: [
        { title: "1", url: "https://t1.picb.cc/uploads/2021/04/04/ZBnLWX.jpg" },
        { title: "2", url: "https://t1.picb.cc/uploads/2021/04/04/ZBnzty.jpg" },
        { title: "3", url: "https://t1.picb.cc/uploads/2021/04/04/ZBnLWX.jpg" },
      ],
    };
  },
  methods: {
    handleSelect(key, keyPath) {
      console.log(key, keyPath);
    },
  },
};
</script>

<style  lang="less" >
.home {
  .menu {
    height: 64px;
    position: relative;
    z-index: 9;
    color: white;
    font-weight: 900;
    .menu_cont {
      width: 100%;
      -webkit-box-shadow: none;
      box-shadow: none;
      position: fixed;
      .menu_cont_item {
        position: relative;
        height: 100%;
        max-width: 1125px;
        width: 90%;
        margin: 0 auto;
        display: flex;
        justify-content: space-between;

        .menu_cont_item_right {
          .el-menu {
            background-color: transparent !important;
            background-image: none;
            &.el-menu.el-menu--horizontal {
              border-bottom: none;
            }
            li {
              color: white;
              background: transparent !important;

              i {
                color: white !important;
              }
              &.is-active {
                background: transparent !important;
              }
              &.is-active:hover {
                background: #67636370 !important;
              }
              &:not(is-active):hover {
                background: #67636370 !important;
              }
            }
          }
        }
        .menu_cont_item_left {
          line-height: 64px;
          /deep/i {
            margin-right: 5px;
            width: 24px;
            text-align: center;
            font-size: 18px;
            vertical-align: middle;
            color: white !important;
          }
        }
      }
    }
  }

  .banner {
    height: 100%;
    width: 100%;
    margin-top: -64px;
    img {
      height: 100%;
      width: 100%;
    }
  }

  .right {
    float: right;
  }
  .left {
    float: left;
  }
}
</style>



